import './App.css';
import ImageCollection from './ImageCollection';
import Section from './Section';
import Button from './Button';
import React, { useEffect, useState } from 'react';
import github from './github.svg'
import { Collapse } from 'bootstrap';

var path = process.env.PUBLIC_URL;
var file = "/img/";

const renderImageData = [
  {
    imageURL: path + file + "extinguisher_render.png",
    alt: "Um extintor low-poly",
  },

  {
    imageURL: path + file + "f15_render.png",
    alt: "Um render",
  },

  {
    imageURL: path + file + "Jet_render.png",
    alt: "Um render",
  },

  {
    imageURL: path + file + "f5-tiger_render.png",
    alt: "Um render",
  },

]


function CollapseContentButton() {
  var [toggle, setToggle] = useState(false);

  useEffect(() => {
      var myCollapse = document.getElementById('collapseTarget')
      var bsCollapse = new Collapse(myCollapse, {toggle: false})
      toggle ? bsCollapse.show() : bsCollapse.hide()
  })

  return (
    <div className='Button'>
        <Button 
          className="btn" 
          btnContent={
            toggle? "Ocultar projetos" : "Ver meus Projetos" 
          } 
          onClick={() => setToggle(toggle => !toggle)}/>
    </div>
  )
}


function App() {

  const [mounted] = useState(true);

  return (
    <div className="App">
      <header className="App-header" >

        <div className='Profile-picture'/>
        <p>
          Ol√°, seja bem vindo.
        </p>
        {
          mounted &&
          <div>
            <CollapseContentButton/>
          </div>
            
        }
      </header>
      <main className='collapse' id='collapseTarget'>
        <Section
            title="Meus projetos pessoais"
            subtitle="Produzidos utilizando Blender e Unity 3D"
        >

        {
          renderImageData.map(function(item){
            return(
              <ImageCollection
                imageURL={item.imageURL}
                alt={item.alt}
              />
            );
          })
        }

        </Section>

      </main>
      <footer className='Foot'>
        <a
          href='https://github.com/BarbosaLeo'
          target="_blank"
          rel='noopener noreferrer'
        >
          <img 
            src={github}
            alt='A Github icon that goes to my Github profile page'
            height="50px"
          />
        </a>
      </footer>
    </div>
  );
}

export default App;